import fontsIcon from '@/components/ui-font/'
export default {
	components:{
		fontsIcon
	},
	props:{
		duration:{
			type:Number,
			default:5000
		},
		noticeListData:{
			type:[Array,Object],
			required:true
		}
	},
	data() {
		return {
			number: 0,
			timer:null,
			flat:false
		}
	},
	watch:{
		flat(news,olds){
			if(!news){
				this.closeTimer()
			}else{
				this.startMove();
			}
		}
	},
	computed:{
		text() {
			return {
				id: this.number,
				val: this.noticeListData[this.number]
			}
		}
	},
	// mounted(){
	// 	this.startMove()
	// },
	methods: {
		startMove () {
		  // eslint-disable-next-line
		  this.timer = setTimeout(() => {
			if (this.number === this.noticeListData.length-1) {
			  this.number = 0;
			} else {
			  this.number += 1;
			}
			this.startMove();
		  }, this.duration); // 滚动不需要停顿则将2000改成动画持续时间
		},
		closeTimer(){
			// 关闭计时器
			clearTimeout(this.timer)
			this.timer=null
		}
	}
}
